export const dataValueSets = {
  all: ['dataEntry/dataValues'],
  byIds: _ref => {
    let {
      dataSetId,
      periodId,
      orgUnitId,
      categoryComboId,
      categoryOptionIds
    } = _ref;
    const params = {
      ds: dataSetId,
      pe: periodId,
      ou: orgUnitId,
      cc: categoryComboId,
      cp: categoryOptionIds && categoryOptionIds.join(';')
    };
    return [...dataValueSets.all, {
      params
    }];
  }
};